<template>
  <div id="forgetPassword">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="page form_page">
      <div class="weui-form">
        <div class="weui-form__text-area">
          <h2 class="weui-form__title">忘记密码</h2>
          <div class="weui-form__desc">请输入您的用户信息</div>
        </div>
        <div class="weui-form__control-area">
          <div class="weui-cells__group weui-cells__group_form">
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户ID</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户ID" v-model="userID">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserID" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">邮箱</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入注册邮箱" v-model="userMail">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserMail" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="weui-form__opr-area">
          <button class="weui-btn weui-btn_primary weui-btn_disabled" id="forgetPasswordBtn" @click="forgetPassword">找回密码</button>
        </div>
        <div class="weui-form__extra-area">
          <div class="weui-footer">
            <p class="weui-footer__links">
              <a class="weui-footer__link" @click="toLogin">前往登录</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import encryption from '../tools/encryption'

export default {
  name: 'ForgetPassword',
  data () {
    return {
      userID: '',
      userMail: '',
      warnMessage: ''
    }
  },
  methods: {
    forgetPassword () {
      if (this.userID === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户ID',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userMail === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户邮箱',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          userID: this.userID,
          userMail: this.userMail
        }
        this.axios.post('/query_user_password', params).then((response) => {
          if (response.data.result === 'wrongInformation') {
            this.$weui.dialog({
              title: '注意',
              content: '您输入的信息有误',
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
          } else if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '您好',
              content: '您的密码是: ' + encryption.decrypt(response.data.userPassword),
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
          } else {
            console.log(response)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    },
    toLogin () {
      if (this.randomQuestionnaire === 'right') {
        this.$router.push({
          path: '/',
          query: {
            randomQuestionnaire: this.randomQuestionnaire,
            randomQuestionnaireNum: this.randomQuestionnaireNum
          }
        })
      } else {
        this.$router.push({ path: '/' })
      }
    }
  },
  watch: {
    userID: function () {
      if (this.userID !== '') {
        document.getElementById('forgetPasswordBtn').setAttribute('class', 'weui-btn weui-btn_primary')
        if (!this.userID.match(/^[0-9]{2,20}$/)) {
          this.warnMessage = '用户id要求由2-20位数字组成'
          document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
          document.getElementById('successUserID').setAttribute('style', 'display:none')
        } else {
          this.warnMessage = ''
          document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
          document.getElementById('successUserID').setAttribute('style', 'display:block')
        }
      } else {
        document.getElementById('forgetPasswordBtn').setAttribute('class', 'weui-btn weui-btn_primary weui-btn_disabled')
      }
    },
    userMail: function () {
      if (!this.userMail.match(/^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/)) {
        this.warnMessage = '请输入正确格式的邮箱'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserMail').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserMail').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
  @media screen and (min-width: 500px) and (max-width: 2000px) {
    #forgetPassword {
      border: 1px solid black;
      border-radius: 10px;
      width: calc(26vw - 2px);
      position: absolute;
      left: 64vw;
      top: 8vw;
      background-color: white;
    }
    .weui-form{
      border-radius: 10px;
    }
  }
  @media screen and (max-width:500px){
    #forgetPassword{
      height: 100%;
    }
    .page{
      height: 100%;
    }
  }
</style>
