<template>
  <div>
    <div class="myTitle">
      <h2>详细信息</h2>
    </div>
    <div class="weui-panel">
      <div v-for="(item,i) in myQuestions" :key="i">
        <div class="wrap">
          <div v-if="item.options_max_num===1">
            <p>题目{{i+1}}（单选）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,j) in item.options.substring(0,item.options.length-1).split(',')" :key="j">
              <div>
                <input type="radio" :name="item.question_id" :value="j" disabled="disabled"
                       :checked="getIfChecked(item.options_max_num,i,j)">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
              </div>
            </div>
          </div>
          <div v-else>
            <p>题目{{i+1}}（多选，请选择1-{{item.options_max_num}}个）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,k) in item.options.substring(0,item.options.length-1).split(',')" :key="k">
              <div>
                <input type="checkbox" :name="item.question_id" :value="k" disabled="disabled"
                       :checked="getIfChecked(item.options_max_num,i,k)">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <router-link to="/submitQuestionnaire">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'MySubmitQuestionnaire',
  data () {
    return {
      questionnaireID: this.$route.query.questionnaireID,
      myQuestions: [],
      myQuestionID: [],
      myAnswer: []
    }
  },
  methods: {
    getIfChecked (num1, num2, num3) {
      if (this.myAnswer.length === this.myQuestions.length) {
        if (num1 === 1) {
          if (parseInt(this.myAnswer[num2].substring(2, this.myAnswer[num2].length - 2)) === num3) {
            return true
          }
          return false
        } else {
          const temp = this.myAnswer[num2].substring(1, this.myAnswer[num2].length - 1).split(', ')
          for (let i = 0; i < temp.length; i++) {
            temp[i] = parseInt(temp[i].substring(1, temp[i].length - 1))
          }
          if (temp.indexOf(num3) > -1) {
            return true
          }
          return false
        }
      }
      return false
    }
  },
  computed: {
    ...mapGetters([
      'getUserID'
    ])
  },
  created () {
    const temp = this
    this.axios.post('/query_question', this.questionnaireID).then((response) => {
      temp.myQuestions = JSON.parse(response.data.myQuestions)
      temp.myQuestions.forEach(function (value, index) {
        temp.myQuestions[index] = value.fields
        temp.myQuestionID.push(temp.myQuestions[index].question_id)
      })
      const params = {
        myQuestionID: temp.myQuestionID,
        userID: temp.getUserID
      }
      temp.axios.post('/query_answer', params).then((response) => {
        if (response.data.result === 'success') {
          temp.myAnswer = response.data.answer
        }
      }).catch(function (error) {
        console.log(error)
      })
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

<style scoped>
  .wrap{
    width:300px;
    margin:20px auto 20px 10px;
  }
</style>
