<template>
  <div id="editMyInformation">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="weui-form">
      <div class="weui-form__text-area">
        <h2 class="weui-form__title">修改资料</h2>
        <div class="weui-form__desc">在此处修改您的个人资料</div>
      </div>
      <div class="weui-form__control-area">
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">用户名</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" v-model="userName">
              </div>
              <div class="weui-cell__hd">
                <i id="successUserName" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">用户邮箱</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" v-model="userMail">
              </div>
              <div class="weui-cell__hd">
                <i id="successUserMail" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">用户单位</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" v-model="userCompany">
              </div>
              <div class="weui-cell__hd">
                <i id="successUserCompany" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="changeMyInformation">修改资料</p>
      </a>
    </div>
    <div class="weui-cells">
      <router-link to="/homePage">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'EditMyInformation',
  data () {
    return {
      userName: this.$store.state.userName,
      userMail: this.$store.state.userMail,
      userCompany: this.$store.state.userCompany,
      warnMessage: ''
    }
  },
  methods: {
    changeMyInformation () {
      if (this.userName === '') {
        this.$weui.dialog({
          title: '注意',
          content: '用户名不能为空',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userMail === '') {
        this.$weui.dialog({
          title: '注意',
          content: '用户邮箱不能为空',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userCompany === '') {
        this.$weui.dialog({
          title: '注意',
          content: '用户单位不能为空',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          userID: this.$store.state.userID,
          userName: this.userName,
          userMail: this.userMail,
          userCompany: this.userCompany
        }
        this.axios.post('/change_user', params).then((response) => {
          if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '注意',
              content: '您的信息已经修改成功',
              buttons: [{
                label: '确定',
                type: 'primary',
                onClick: function () {
                  location.reload()
                }
              }]
            })

            sessionStorage.setItem('userName', this.userName)
            sessionStorage.setItem('userMail', this.userMail)
            sessionStorage.setItem('userCompany', this.userCompany)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    }
  },
  watch: {
    userName: function () {
      if (!this.userName.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
        this.warnMessage = '请输入正确的用户名（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserName').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserName').setAttribute('style', 'display:block')
      }
    },
    userMail: function () {
      if (!this.userMail.match(/^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/)) {
        this.warnMessage = '请输入正确格式的邮箱'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserMail').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserMail').setAttribute('style', 'display:block')
      }
    },
    userCompany: function () {
      if (!this.userCompany.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
        this.warnMessage = '请输入正确的用户单位（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserCompany').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserCompany').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
  p{
    color: black;
  }
</style>
