<template>
  <div id="login">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="page form_page">
      <div class="weui-form">
        <div class="weui-form__text-area">
          <h2 class="weui-form__title">登录</h2>
          <div class="weui-form__desc">请输入您的用户信息</div>
        </div>
        <div class="weui-form__control-area">
          <div class="weui-cells__group weui-cells__group_form">
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户名</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户名" v-model="userName">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserName" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">密码</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="password" class="weui-input" placeholder="请输入密码" v-model="userPassword">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserPassword" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="weui-form__opr-area">
          <button class="weui-btn weui-btn_primary weui-btn_disabled" id="loginBtn" @click="login">登录</button>
        </div>
        <div class="weui-form__extra-area">
          <div class="weui-footer">
            <p class="weui-footer__links">
              <a class="weui-footer__link" @click="toRegister">注册账号</a>
              <a class="weui-footer__link" @click="toForgetPassword">忘记密码</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import encryption from '../tools/encryption'

export default {
  name: 'Login',
  data () {
    return {
      userName: '',
      userPassword: '',
      randomQuestionnaire: this.$route.query.randomQuestionnaire,
      randomQuestionnaireNum: this.$route.query.randomQuestionnaireNum,
      warnMessage: ''
    }
  },
  methods: {
    login () {
      if (this.userName === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户名',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userPassword === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户密码',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          userName: this.userName,
          userPassword: encryption.encryption(this.userPassword)
        }
        this.axios.post('/login_user', params).then((response) => {
          if (response.data.result === 'noUser') {
            this.$store.commit('changeIfLogin', false)
            this.$weui.dialog({
              title: '注意',
              content: '该用户尚未注册',
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
          } else if (response.data.result === 'wrongPassword') {
            this.$store.commit('changeIfLogin', false)
            this.$weui.dialog({
              title: '注意',
              content: '请输入正确的密码',
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
          } else if (response.data.result === 'success') {
            this.$store.commit('changeIfLogin', true)
            this.$store.commit('changeUserName', this.userName)
            this.$store.commit('changeUserID', response.data.userID)
            this.$store.commit('changeUserMail', response.data.userMail)
            this.$store.commit('changeUserCompany', response.data.userCompany)

            sessionStorage.clear()
            sessionStorage.setItem('userName', this.userName)
            sessionStorage.setItem('userID', response.data.userID)
            sessionStorage.setItem('userMail', response.data.userMail)
            sessionStorage.setItem('userCompany', response.data.userCompany)
            if (this.randomQuestionnaire === 'right') {
              this.$router.push({
                path: '/questionnaire',
                query: {
                  randomQuestionnaireNum: this.randomQuestionnaireNum
                }
              })
            } else {
              this.$router.push({ path: '/homePage' })
            }
          } else {
            console.log(response)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    },
    toRegister () {
      if (this.randomQuestionnaire === 'right') {
        this.$router.push({
          path: '/register',
          query: {
            randomQuestionnaire: this.randomQuestionnaire,
            randomQuestionnaireNum: this.randomQuestionnaireNum
          }
        })
      } else {
        this.$router.push({ path: '/register' })
      }
    },
    toForgetPassword () {
      if (this.randomQuestionnaire === 'right') {
        this.$router.push({
          path: '/forgetPassword',
          query: {
            randomQuestionnaire: this.randomQuestionnaire,
            randomQuestionnaireNum: this.randomQuestionnaireNum
          }
        })
      } else {
        this.$router.push({ path: '/forgetPassword' })
      }
    }
  },
  watch: {
    userName: function () {
      if (this.userName !== '') {
        document.getElementById('loginBtn').setAttribute('class', 'weui-btn weui-btn_primary')
        if (!this.userName.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
          this.warnMessage = '请输入正确的用户名（汉字字母数字下划线组成的2-20位字符）'
          document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
          document.getElementById('successUserName').setAttribute('style', 'display:none')
        } else {
          this.warnMessage = ''
          document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
          document.getElementById('successUserName').setAttribute('style', 'display:block')
        }
      } else {
        document.getElementById('loginBtn').setAttribute('class', 'weui-btn weui-btn_primary weui-btn_disabled')
      }
    },
    userPassword: function () {
      if (!this.userPassword.match(/^[a-zA-Z0-9_]{2,18}$/)) {
        this.warnMessage = '请输入正确的密码（字母数字组成的2-18位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserPassword').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserPassword').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
  @media screen and (min-width: 500px) and (max-width: 2000px) {
    #login {
      border: 1px solid black;
      border-radius: 10px;
      width: calc(26vw - 2px);
      position: absolute;
      left: 64vw;
      top: 8vw;
      background-color: white;
    }
    .weui-form{
      border-radius: 10px;
    }
  }
  @media screen and (max-width:500px){
    #login{
      height: 100%;
    }
    .page{
      height: 100%;
    }
  }
</style>
