import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../views/Login'
import Register from '../views/Register'
import HomePage from '../views/HomePage'
import MyQuestionnaire from '../views/MyQuestionnaire'
import EditQuestionnaire from '../views/EditQuestionnaire'
import Questionnaire from '../views/Questionnaire'
import SubmitQuestionnaire from '../views/SubmitQuestionnaire'
import MySubmitQuestionnaire from '../views/MySubmitQuestionnaire'
import Result from '../views/Result'
import ForgetPassword from '../views/ForgetPassword'
import EditMyInformation from '../views/EditMyInformation'
import EditMyPassword from '../views/EditMyPassword'
import QuestionnaireInformation from '../views/QuestionnaireInformation'
import ChoiceInformation from '../views/ChoiceInformation'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/forgetPassword',
    name: 'ForgetPassword',
    component: ForgetPassword
  },
  {
    path: '/homePage',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/editMyInformation',
    name: 'EditMyInformation',
    component: EditMyInformation
  },
  {
    path: '/editMyPassword',
    name: 'EditMyPassword',
    component: EditMyPassword
  },
  {
    path: '/myQuestionnaire',
    name: 'MyQuestionnaire',
    component: MyQuestionnaire
  },
  {
    path: '/questionnaireInformation',
    name: 'QuestionnaireInformation',
    component: QuestionnaireInformation
  },
  {
    path: '/editQuestionnaire',
    name: 'EditQuestionnaire',
    component: EditQuestionnaire
  },
  {
    path: '/choiceInformation',
    name: 'ChoiceInformation',
    component: ChoiceInformation
  },
  {
    path: '/questionnaire',
    name: 'Questionnaire',
    component: Questionnaire
  },
  {
    path: '/submitQuestionnaire',
    name: 'SubmitQuestionnaire',
    component: SubmitQuestionnaire
  },
  {
    path: '/mySubmitQuestionnaire',
    name: 'MySubmitQuestionnaire',
    component: MySubmitQuestionnaire
  },
  {
    path: '/result',
    name: 'Result',
    component: Result
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
