<template>
  <div id="register">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="page form_page">
      <div class="weui-form">
        <div class="weui-form__text-area">
          <h2 class="weui-form__title">注册</h2>
          <div class="weui-form__desc">请输入您的注册信息</div>
        </div>
        <div class="weui-form__control-area">
          <div class="weui-cells__group weui-cells__group_form">
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户ID</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户ID" v-model="userID">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserID" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户名</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户名" v-model="userName">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserName" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户邮箱</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户邮箱" v-model="userMail">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserMail" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">密码</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="password" class="weui-input" placeholder="请输入密码" v-model="userPassword">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserPassword" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <label class="weui-label">用户单位</label>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="请输入用户单位" v-model="userCompany">
                </div>
                <div class="weui-cell__hd">
                  <i id="successUserCompany" class="weui-icon-success weui-icon_msg" style="display: none"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="weui-form__opr-area">
          <button class="weui-btn weui-btn_primary weui-btn_disabled" id="registerBtn" @click="addUser">注册</button>
        </div>
        <div class="weui-form__extra-area">
          <div class="weui-footer">
            <p class="weui-footer__links">
              <a class="weui-footer__link" @click="toLogin">前往登录</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import encryption from '../tools/encryption'

export default {
  name: 'Register',
  data () {
    return {
      userID: '',
      userName: '',
      userMail: '',
      userPassword: '',
      userCompany: '',
      warnMessage: '',
      randomQuestionnaire: this.$route.query.randomQuestionnaire,
      randomQuestionnaireNum: this.$route.query.randomQuestionnaireNum
    }
  },
  methods: {
    addUser () {
      if (this.userID === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户ID',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userName === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户名',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userMail === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户邮箱',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userPassword === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写用户密码',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.userCompany === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写公司',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          userID: this.userID,
          userName: this.userName,
          userMail: this.userMail,
          userPassword: encryption.encryption(this.userPassword),
          userCompany: this.userCompany
        }
        this.axios.post('/register_user', params).then((response) => {
          if (response.data.result === 'registered') {
            this.$weui.dialog({
              title: '注意',
              content: '该用户id已经被使用过',
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
            this.userID = ''
            document.getElementById('successUserID').setAttribute('style', 'display:none')
          } else if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '恭喜',
              content: '已成功注册',
              buttons: [{
                label: '确定',
                type: 'primary',
                onClick: function () {
                  location.reload()
                }
              }]
            })
          } else {
            console.log(encryption.encryption(this.userPassword))
            console.log(response)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    },
    toLogin () {
      if (this.randomQuestionnaire === 'right') {
        this.$router.push({
          path: '/',
          query: {
            randomQuestionnaire: this.randomQuestionnaire,
            randomQuestionnaireNum: this.randomQuestionnaireNum
          }
        })
      } else {
        this.$router.push({ path: '/' })
      }
    }
  },
  watch: {
    userID: function () {
      if (this.userID !== '') {
        document.getElementById('registerBtn').setAttribute('class', 'weui-btn weui-btn_primary')
        if (!this.userID.match(/^[0-9]{2,20}$/)) {
          this.warnMessage = '用户id要求由2-20位数字组成'
          document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
          document.getElementById('successUserID').setAttribute('style', 'display:none')
        } else {
          this.warnMessage = ''
          document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
          document.getElementById('successUserID').setAttribute('style', 'display:block')
        }
      } else {
        document.getElementById('registerBtn').setAttribute('class', 'weui-btn weui-btn_primary weui-btn_disabled')
      }
    },
    userName: function () {
      if (!this.userName.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
        this.warnMessage = '请输入正确的用户名（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserName').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserName').setAttribute('style', 'display:block')
      }
    },
    userMail: function () {
      if (!this.userMail.match(/^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/)) {
        this.warnMessage = '请输入正确格式的邮箱'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserMail').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserMail').setAttribute('style', 'display:block')
      }
    },
    userPassword: function () {
      if (!this.userPassword.match(/^[0-9]{2,20}$/)) {
        this.warnMessage = '请输入正确的密码'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserPassword').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserPassword').setAttribute('style', 'display:block')
      }
    },
    userCompany: function () {
      if (!this.userCompany.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
        this.warnMessage = '请输入正确的用户单位（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successUserCompany').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successUserCompany').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
  @media screen and (min-width: 500px) and (max-width: 2000px) {
    #register {
      border: 1px solid black;
      border-radius: 10px;
      width: calc(26vw - 2px);
      position: absolute;
      left: 64vw;
      top: 8vw;
      background-color: white;
    }
    .weui-form{
      border-radius: 10px;
    }
  }
  @media screen and (max-width:500px){
    #register{
      height: 100%;
    }
    .page{
      height: 100%;
    }
  }
</style>
