<template>
  <div id="choiceInformation">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="weui-form">
      <div class="weui-form__text-area">
        <h2 class="weui-form__title">添加一个问题</h2>
      </div>
      <div class="weui-form__control-area">
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">题目</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" placeholder="请输入题目" v-model="questionName">
              </div>
              <div class="weui-cell__hd">
                <i id="successQuestionName" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">可选择个数</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" placeholder="请输入最大可选择个数" v-model="optionsMaxNum">
              </div>
              <div class="weui-cell__hd">
                <i id="successOptionsMaxNum" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
          </div>
        </div>
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells__title">选项1</div>
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell">
                <div class="weui-cell__bd">
                  <textarea class="weui-textarea" placeholder="请输入一个选项" v-model="option1" rows="3"></textarea>
                </div>
              </div>
          </div>
        </div>
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells__title">选项2</div>
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell">
              <div class="weui-cell__bd">
                <textarea class="weui-textarea" placeholder="请输入一个选项" v-model="option2" rows="3"></textarea>
              </div>
            </div>
          </div>
        </div>
        <div v-for="(item,i) in otherOptions" :key="i">
          <div class="weui-cells__group weui-cells__group_form">
            <div class="weui-cells__title">选项{{i+3}}</div>
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell">
                <div class="weui-cell__bd">
                  <textarea class="weui-textarea" placeholder="请输入一个选项" v-model="otherOptions[i]" rows="3"></textarea>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="addOption">再添加一个选项</p>
      </a>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="deleteOption">删除一个选项</p>
      </a>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="submitSingleChoice">提交</p>
      </a>
    </div>
    <div class="weui-cells">
      <router-link :to="{path:'/editQuestionnaire',query:{questionnaireID:questionnaireID}}">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SingleChoice',
  data () {
    return {
      questionName: '',
      option1: '',
      option2: '',
      otherOptions: [],
      allOptions: [],
      optionsMaxNum: '',
      questionnaireID: this.$route.query.questionnaireID,
      warnMessage: ''
    }
  },
  methods: {
    addOption () {
      this.otherOptions.push('')
    },
    deleteOption () {
      this.otherOptions.pop()
    },
    submitSingleChoice () {
      this.allOptions = JSON.parse(JSON.stringify(this.otherOptions))
      this.allOptions.unshift(this.option1, this.option2)
      if (this.questionName === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写问题名',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.allOptions.indexOf('') !== -1) {
        this.$weui.dialog({
          title: '注意',
          content: '请补全选项信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          questionnaireID: this.questionnaireID,
          questionName: this.questionName,
          options: this.allOptions,
          optionsMaxNum: parseInt(this.optionsMaxNum)
        }
        this.axios.post('/add_single_choice', params).then((response) => {
          if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '您好',
              content: '您已成功添加一道题目',
              buttons: [{
                label: '确定',
                type: 'primary',
                onClick: function () {
                  location.reload()
                }
              }]
            })
          }
        }).catch(function (error) {
          window.console.log(error)
        })
      }
    }
  },
  watch: {
    questionName: function () {
      if (!this.questionName.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,60}$/)) {
        this.warnMessage = '请输入正确的题目（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successQuestionName').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successQuestionName').setAttribute('style', 'display:block')
      }
    },
    optionsMaxNum: function () {
      if (!this.optionsMaxNum.match(/^[0-9]{1,3}$/)) {
        this.warnMessage = '请输入正确的可选择个数（1-3位数字）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successOptionsMaxNum').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successOptionsMaxNum').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
</style>
