export default {
  getIfLogin (state) {
    return state.ifLogin
  },
  getUserName (state) {
    return state.userName
  },
  getUserID (state) {
    return state.userID
  },
  getUserMail (state) {
    return state.userMail
  },
  getUserCompany (state) {
    return state.userCompany
  }
}
