<template>
  <div id="myQuestionnaire">
    <div class="main">
      <div v-if="myQuestionnaire.length===0">
        <div class="myTitle">
          <h3>我的调查问卷</h3>
          <p>您还没有调查问卷，创建一个吧</p>
        </div>
      </div>
      <div v-else>
        <div class="myTitle">
          <h3>我的调查问卷</h3>
          <p>在此处编辑我的调查问卷</p>
        </div>
        <div class="weui-form__control-area">
          <div class="weui-cells__group weui-cells__group_form">
            <div class="weui-cells weui-cells_form">
              <div class="weui-cell weui-cell_active">
                <div class="weui-cell__hd">
                  <a @click="chooseQuestionnaireType" class="weui-btn weui-btn_mini weui-btn_primary">筛选</a>
                </div>
                <div class="weui-cell__hd">
                  <input type="text" class="weui-input" placeholder="全部调查问卷" v-model="questionnaireType" readonly>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-for="(item,i) in myQuestionnaire" :key="i">
          <div v-if="item.questionnaire_state&&(questionnaireType==='全部调查问卷'||questionnaireType==='已发布调查问卷')">
            <div class="weui-form-preview">
              <div class="weui-form-preview__hd">
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">问卷名</label>
                  <em class="weui-form-preview__value">{{item.questionnaire_name}}</em>
                </div>
              </div>
              <div class="weui-form-preview__bd">
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">开始时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.start_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">结束时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.end_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">创建时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.add_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">状态</label>
                  <div v-if="compareTime(new Date(item.end_time))">
                    <span class="weui-form-preview__value">时间已截止</span>
                  </div>
                  <div v-else>
                    <span class="weui-form-preview__value">{{getState(item.questionnaire_state)}}</span>
                  </div>
                </div>
              </div>
              <div class="weui-form-preview__ft">
                <a class="weui-form-preview__btn weui-form-preview__btn_default">
                  <p @click="addResult(item.questionnaire_id,item.end_time)">查看结果</p>
                </a>
                <button @click="showPublishInformation(item.questionnaire_id)" class="weui-form-preview__btn weui-form-preview__btn_default">发布信息</button>
              </div>
            </div>
          </div>
          <div v-if="(!item.questionnaire_state)&&(questionnaireType==='全部调查问卷'||questionnaireType==='未发布调查问卷')">
            <div class="weui-form-preview">
              <div class="weui-form-preview__hd">
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">问卷名</label>
                  <em class="weui-form-preview__value">{{item.questionnaire_name}}</em>
                </div>
              </div>
              <div class="weui-form-preview__bd">
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">开始时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.start_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">结束时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.end_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">创建时间</label>
                  <span class="weui-form-preview__value">{{(new Date(item.add_time)).toLocaleString()}}</span>
                </div>
                <div class="weui-form-preview__item">
                  <label class="weui-form-preview__label">状态</label>
                  <div v-if="compareTime(new Date(item.end_time))">
                    <span class="weui-form-preview__value">时间已截止</span>
                  </div>
                  <div v-else>
                    <span class="weui-form-preview__value">{{getState(item.questionnaire_state)}}</span>
                  </div>
                </div>
              </div>
              <div class="weui-form-preview__ft">
                <a class="weui-form-preview__btn weui-form-preview__btn_default">
                  <router-link
                    :to="{path:'/editQuestionnaire',query:{questionnaireID:item.questionnaire_id.toString()}}">
                    <p>编辑</p>
                  </router-link>
                </a>
                <button @click="publish(item.questionnaire_id)" class="weui-form-preview__btn weui-form-preview__btn_primary"><p>现在发布</p></button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="operate">
        <div class="weui-cells">
          <router-link to="/questionnaireInformation">
            <a class="weui-cell weui-cell_access">
              <div class="weui-cell__bd">
                <p>新增调查问卷</p>
              </div>
              <div class="weui-cell__ft"></div>
            </a>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'MyQuestionnaire',
  data () {
    return {
      showQuestionnaireDialog: false,
      myQuestionnaire: [],
      questionnaireType: '全部调查问卷'
    }
  },
  computed: {
    ...mapGetters([
      'getUserID'
    ])
  },
  created () {
    const temp = this
    this.axios.post('/query_questionnaire', this.getUserID).then((response) => {
      temp.myQuestionnaire = JSON.parse(response.data.myQuestionnaire)
      temp.myQuestionnaire.forEach(function (value, index) {
        temp.myQuestionnaire[index] = value.fields
      })
    }).catch(function (error) {
      // console.log(error)
      alert(error)
    })
  },
  methods: {
    compareTime (param) {
      const now = new Date()
      if (now > param) {
        return true
      } else {
        return false
      }
    },
    getState (param) {
      if (param) {
        return '已发布'
      }
      return '未发布'
    },
    publish (param) {
      this.axios.post('/change_state', param).then((response) => {
        if (response.data.result === 'noQuestion') {
          this.$weui.dialog({
            title: '您好',
            content: '该调查问卷没有添加题目',
            buttons: [{
              label: '确定',
              type: 'primary'
            }]
          })
        } else if (response.data.result === 'success') {
          this.$weui.dialog({
            title: '通知',
            content: '发布成功',
            buttons: [{
              label: '确定',
              type: 'primary',
              onClick: function () {
                location.reload()
              }
            }]
          })
        } else {
          // console.log(response)
          alert(response)
        }
      }).catch(function (error) {
        // console.log(error)
        alert(error)
      })
    },
    showPublishInformation (questionnaireID) {
      this.$weui.dialog({
        title: '您好',
        content: '发布链接为：http://192.168.0.100:8080/?randomQuestionnaire=right&randomQuestionnaireNum=' + questionnaireID,
        buttons: [{
          label: '确定',
          type: 'primary'
        }]
      })
    },
    addResult (questionnaireID, endTime) {
      const now = new Date()
      if (now < new Date(endTime)) {
        this.$weui.dialog({
          title: '您好',
          content: '还未到截止时间',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        this.axios.post('add_result', questionnaireID).then((response) => {
        }).catch(function (error) {
          // console.log(error)
          alert(error)
        })
        this.$router.push({
          path: '/result',
          query: { questionnaireID: questionnaireID.toString() }
        })
      }
    },
    chooseQuestionnaireType () {
      const temp = this
      this.$weui.picker([
        {
          label: '全部调查问卷',
          value: 0
        },
        {
          label: '已发布调查问卷',
          value: 1
        },
        {
          label: '未发布调查问卷',
          value: 2
        }
      ], {
        onConfirm: function (result) {
          temp.questionnaireType = result[0].label
        }
      })
    }
  }
}
</script>

<style scoped>
  .weui-cells{
    margin-bottom: 10%;
  }
  .operate{
    margin-top: 10%;
    margin-bottom: 10%;
  }
  p{
    color: black;
  }
  .weui-form-preview{
    margin-bottom: 10%;
  }
</style>
