<template>
  <div id="editQuestionnaire">
    <div class="myTitle">
      <h2>编辑调查问卷</h2>
      <p>{{questionnaireID}}</p>
    </div>
    <div class="weui-panel">
      <div v-for="(item,i) in myQuestions" :key="i">
        <div class="wrap">
          <div v-if="item.options_max_num===1">
            <p>题目{{i+1}}（单选）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,j) in item.options.substring(0,item.options.length-1).split(',')" :key="j">
              <div>
                <input type="radio" :name="item.question_id" :value="j">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
              </div>
            </div>
          </div>
          <div v-else>
            <p>题目{{i+1}}（多选，请选择1-{{item.options_max_num}}个）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,k) in item.options.substring(0,item.options.length-1).split(',')" :key="k">
              <div>
                <input type="checkbox" :name="item.question_id" :value="k"
                       @click="checkNum(item.question_id,item.options_max_num)">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="operate">
      <div class="weui-cells">
        <router-link :to="{path:'/choiceInformation',query:{questionnaireID:questionnaireID}}">
          <a class="weui-btn_cell weui-btn_cell-default">
            <p>继续添加问题</p>
          </a>
        </router-link>
      </div>
      <div class="weui-cells">
        <router-link to="/myQuestionnaire">
          <a class="weui-btn_cell weui-btn_cell-default">
            <p>返回</p>
          </a>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'EditQuestionnaire',
  data () {
    return {
      questionnaireID: this.$route.query.questionnaireID,
      myQuestions: []
    }
  },
  methods: {
    checkNum (name, maxNum) {
      const temp = document.getElementsByName(name)
      let num = 0
      for (let i = 0; i < temp.length; i++) {
        if (temp[i].checked) {
          num++
        }
      }
      if (num < maxNum) {
        for (let i = 0; i < temp.length; i++) {
          if (!temp[i].checked) {
            temp[i].disabled = ''
          }
        }
      } else if (num === maxNum) {
        for (let i = 0; i < temp.length; i++) {
          if (!temp[i].checked) {
            temp[i].disabled = 'disabled'
          }
        }
      }
    }
  },
  created () {
    const temp = this
    this.axios.post('/query_question', this.questionnaireID).then((response) => {
      temp.myQuestions = JSON.parse(response.data.myQuestions)
      temp.myQuestions.forEach(function (value, index) {
        temp.myQuestions[index] = value.fields
      })
      console.log(temp.myQuestions)
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

<style scoped>
.wrap{
  width:300px;
  margin:20px auto 20px 10px;
}
</style>
