<template>
  <div id="questionnaireInformation">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="weui-form">
      <div class="weui-form__text-area">
        <h2 class="weui-form__title">创建调查问卷</h2>
        <div class="weui-form__desc">请输入您的调查问卷信息</div>
      </div>
      <div class="weui-form__control-area">
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">问卷名称</label>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" placeholder="请输入调查问卷名" v-model="questionnaireName">
              </div>
              <div class="weui-cell__hd">
                <i id="successQuestionnaireName" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <a @click="chooseStartDay" class="weui-btn weui-btn_mini weui-btn_primary">开始</a>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" placeholder="请选择起始时间" v-model="theStartTime" readonly>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <a @click="chooseEndDay" class="weui-btn weui-btn_mini weui-btn_primary">结束</a>
              </div>
              <div class="weui-cell__hd">
                <input type="text" class="weui-input" placeholder="请选择截止时间" v-model="theEndTime" readonly>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="submitQuestionnaire">提交</p>
      </a>
    </div>
    <div class="weui-cells">
      <router-link to="/myQuestionnaire">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'QuestionnaireInformation',
  data () {
    return {
      questionnaireName: '',
      startYear: '',
      startMonth: '',
      startDay: '',
      startHour: '',
      startMinute: '',
      startTime: '',
      theStartTime: '',
      endYear: '',
      endMonth: '',
      endDay: '',
      endHour: '',
      endMinute: '',
      endTime: '',
      theEndTime: '',
      warnMessage: ''
    }
  },
  computed: {
    ...mapGetters([
      'getUserID'
    ])
  },
  methods: {
    chooseStartDay () {
      const temp = this
      this.$weui.datePicker({
        start: new Date(),
        end: 2030,
        onConfirm: function (result) {
          temp.startYear = result[0].value
          temp.startMonth = result[1].value
          temp.startDay = result[2].value
        },
        onClose: function () {
          temp.$options.methods.chooseStartTime.bind(temp)()
        },
        id: 'datePicker'
      })
    },
    chooseStartTime () {
      const hourArray = []
      const minuteArray = []
      for (let i = 0; i < 24; i++) {
        hourArray.push({
          label: i,
          value: i
        })
      }
      for (let j = 0; j < 60; j++) {
        minuteArray.push({
          label: j,
          value: j
        })
      }
      const temp = this
      this.$weui.picker(
        hourArray,
        minuteArray,
        {
          onConfirm: function (result) {
            temp.startHour = result[0].value
            temp.startMinute = result[1].value
            temp.startTime = new Date(temp.startYear, temp.startMonth - 1, temp.startDay, temp.startHour, temp.startMinute)
            temp.theStartTime = temp.startTime.toLocaleString()
          },
          id: 'picker'
        }
      )
    },
    chooseEndDay () {
      if (this.startTime === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请先选择起始时间',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const temp = this
        this.$weui.datePicker({
          start: new Date(),
          end: 2030,
          onConfirm: function (result) {
            temp.endYear = result[0].value
            temp.endMonth = result[1].value
            temp.endDay = result[2].value
          },
          onClose: function () {
            temp.$options.methods.chooseEndTime.bind(temp)()
          },
          id: 'datePicker'
        })
      }
    },
    chooseEndTime () {
      const hourArray = []
      const minuteArray = []
      for (let i = 0; i < 24; i++) {
        hourArray.push({
          label: i,
          value: i
        })
      }
      for (let j = 0; j < 60; j++) {
        minuteArray.push({
          label: j,
          value: j
        })
      }
      const temp = this
      this.$weui.picker(
        hourArray,
        minuteArray,
        {
          onConfirm: function (result) {
            temp.endHour = result[0].value
            temp.endMinute = result[1].value
            temp.endTime = new Date(temp.endYear, temp.endMonth - 1, temp.endDay, temp.endHour, temp.endMinute)
            const now = new Date()
            if (now > temp.endTime) {
              temp.$weui.dialog({
                title: '注意',
                content: '截止时间应晚于现在',
                buttons: [{
                  label: '确定',
                  type: 'primary'
                }]
              })
            } else if (temp.endTime <= temp.startTime) {
              temp.$weui.dialog({
                title: '注意',
                content: '截止时间应晚于开始时间',
                buttons: [{
                  label: '确定',
                  type: 'primary'
                }]
              })
            } else {
              temp.theEndTime = temp.endTime.toLocaleString()
            }
          },
          id: 'picker'
        }
      )
    },
    submitQuestionnaire () {
      if (this.questionnaireName === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写调查问卷名称',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.startTime === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请选择起始时间',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.endTime === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请选择截止时间',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          userID: this.getUserID,
          questionnaireName: this.questionnaireName,
          startTime: this.startTime.valueOf(),
          endTime: this.endTime.valueOf()
        }
        this.axios.post('/add_questionnaire', params).then((response) => {
          if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '注意',
              content: '调查问卷创建成功',
              buttons: [{
                label: '确定',
                type: 'primary',
                onClick: function () {
                  location.reload()
                }
              }]
            })
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    }
  },
  watch: {
    questionnaireName: function () {
      if (!this.questionnaireName.match(/^[\u4E00-\u9FA5a-zA-Z0-9_]{2,20}$/)) {
        this.warnMessage = '请输入正确的调查问卷名称（汉字字母数字下划线组成的2-20位字符）'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successQuestionnaireName').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successQuestionnaireName').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
</style>
