<template>
  <div>
    <div v-if="submit">
      <div class="myTitle">
        <p>您已提交过该调查问卷，可前往我的调查问卷查看相关信息</p>
      </div>
    </div>
    <div v-else>
      <div class="myTitle">
        <h2>调查问卷</h2>
        <p>{{questionnaireName}}</p>
      </div>
      <div class="weui-panel">
          <div v-for="(item,i) in myQuestions" :key="i">
            <div class="wrap">
              <div v-if="item.options_max_num===1">
                <p>题目{{i+1}}（单选）</p>
                <p>{{item.question_name}}</p>
                <div v-for="(choice,j) in item.options.substring(0,item.options.length-1).split(',')" :key="j">
                  <div>
                    <input type="radio" :name="item.question_id" :value="j">
                    <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
                  </div>
                </div>
              </div>
              <div v-else>
                <p>题目{{i+1}}（多选，请选择1-{{item.options_max_num}}个）</p>
                <p>{{item.question_name}}</p>
                <div v-for="(choice,k) in item.options.substring(0,item.options.length-1).split(',')" :key="k">
                  <div>
                    <input type="checkbox" :name="item.question_id" :value="k"
                           @click="checkNum(item.question_id,item.options_max_num)">
                    <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      <div class="weui-cells">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p @click="submitRandomQuestionnaire()">提交</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'Questionnaire',
  data () {
    return {
      questionnaireID: this.$route.query.randomQuestionnaireNum,
      myQuestions: [],
      questionnaireName: '',
      results: {},
      submit: false
    }
  },
  computed: {
    ...mapGetters([
      'getUserID'
    ])
  },
  methods: {
    submitRandomQuestionnaire () {
      for (let i = 0; i < this.myQuestions.length; i++) {
        if (this.myQuestions[i].options_max_num === 1) {
          this.results[this.myQuestions[i].question_id] = this.getRadio(this.myQuestions[i].question_id)
        }
        this.results[this.myQuestions[i].question_id] = this.getCheckbox(this.myQuestions[i].question_id)
      }
      const params = {
        results: this.results,
        userID: this.getUserID,
        questionnaireID: this.questionnaireID
      }
      this.axios.post('/add_answer', params).then((response) => {
        if (response.data.result === 'success') {
          this.$weui.dialog({
            title: '恭喜',
            content: '已成功提交',
            buttons: [{
              label: '确定',
              type: 'primary',
              onClick: function () {
                location.reload()
              }
            }]
          })
        }
      }).catch(function (error) {
        console.log(error)
      })
    },
    getRadio (name) {
      const temp = document.getElementsByName(name)
      for (let i = 0; i < temp.length; i++) {
        if (temp[i].checked) {
          return temp[i].value
        }
      }
    },
    getCheckbox (name) {
      const result = []
      const temp = document.getElementsByName(name)
      for (let i = 0; i < temp.length; i++) {
        if (temp[i].checked) {
          result.push(temp[i].value)
        }
      }
      return result
    },
    checkNum (name, maxNum) {
      const temp = document.getElementsByName(name)
      let num = 0
      for (let i = 0; i < temp.length; i++) {
        if (temp[i].checked) {
          num++
        }
      }
      if (num < maxNum) {
        for (let i = 0; i < temp.length; i++) {
          if (!temp[i].checked) {
            temp[i].disabled = ''
          }
        }
      } else if (num === maxNum) {
        for (let i = 0; i < temp.length; i++) {
          if (!temp[i].checked) {
            temp[i].disabled = 'disabled'
          }
        }
      }
    }
  },
  created () {
    const params = {
      questionnaireID: this.questionnaireID,
      userID: this.getUserID
    }

    const temp = this
    this.axios.post('/query_submit', params).then((response) => {
      if (response.data.submit === 'true') {
        temp.submit = true
      } else {
        this.axios.post('/query_question', this.questionnaireID).then((response) => {
          temp.questionnaireName = response.data.questionnaireName
          temp.myQuestions = JSON.parse(response.data.myQuestions)
          temp.myQuestions.forEach(function (value, index) {
            temp.myQuestions[index] = value.fields
          })
        }).catch(function (error) {
          console.log(error)
        })
      }
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

<style scoped>
  .wrap{
    width:300px;
    margin:20px auto 20px 10px;
  }
</style>
