<template>
  <div>
    <div class="myTitle">
      <h2>查看调查问卷结果</h2>
      <p>共有{{peopleNumber}}人参与</p>
    </div>
    <div class="weui-panel">
      <div v-for="(item,i) in myQuestions" :key="i">
        <div class="wrap">
          <div v-if="item.options_max_num===1">
            <p>题目{{i+1}}（单选）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,j) in item.options.substring(0,item.options.length-1).split(',')" :key="j">
              <div class="choice">
                <div v-if="getResult" class="mask"
                     :style="{width:toPercent(result[i][j]/getSum(result[i])),backgroundColor:getColor(j)}"></div>
                <input type="radio" :name="item.question_id" :value="j" disabled="disabled">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
                <span v-if="getResult">  {{result[i][j]}}人 占比{{toPercent(result[i][j]/getSum(result[i]))}}</span>
              </div>
            </div>
          </div>
          <div v-else>
            <p>题目{{i+1}}（多选，请选择1-{{item.options_max_num}}个）</p>
            <p>{{item.question_name}}</p>
            <div v-for="(choice,k) in item.options.substring(0,item.options.length-1).split(',')" :key="k">
              <div class="choice">
                <div v-if="getResult" class="mask"
                     :style="{width:toPercent(result[i][k]/getSum(result[i])),backgroundColor:getColor(k)}"></div>
                <input type="checkbox" :name="item.question_id" :value="k" disabled="disabled">
                <label :for="choice.substring(2,choice.length-1)">{{choice.substring(2,choice.length-1)}}</label>
                <span v-if="getResult">  {{result[i][k]}}人 占比{{toPercent(result[i][k]/getSum(result[i]))}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="weui-cells">
      <router-link to="/myQuestionnaire">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Result',
  data () {
    return {
      questionnaireID: this.$route.query.questionnaireID,
      myQuestions: [],
      result: [],
      getResult: false,
      peopleNumber: 0
    }
  },
  methods: {
    getSum (array) {
      let sum = 0
      for (let i = 0; i < array.length; i++) {
        sum += array[i]
      }
      return sum
    },
    toPercent (number) {
      let temp = Number(number * 100).toFixed(1)
      temp += '%'
      return temp
    },
    getColor (num) {
      const color = ['pink', 'aqua', 'orange', 'navy', 'fuchsia', 'green', 'maroon', 'purple', 'olive', 'red', 'lime']
      return color[num % 11]
    }
  },
  created () {
    const temp = this
    this.axios.post('/query_question', this.questionnaireID).then((response) => {
      temp.myQuestions = JSON.parse(response.data.myQuestions)
      temp.myQuestions.forEach(function (value, index) {
        temp.myQuestions[index] = value.fields
      })
      temp.axios.post('/query_result', temp.questionnaireID).then((response) => {
        let result = response.data.allResult
        temp.peopleNumber = response.data.peopleNumber
        result = result.substring(2, result.length - 2).split('], [')
        for (let i = 0; i < result.length; i++) {
          result[i] = result[i].split(', ')
          for (let j = 0; j < result[i].length; j++) {
            result[i][j] = parseInt(result[i][j])
          }
        }
        temp.result = result
        temp.getResult = true
      }).catch(function (error) {
        console.log(error)
      })
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

<style scoped>
  .wrap{
    width:300px;
    margin:20px auto 20px 10px;
  }
  .choice{
    position: relative;
  }
  .choice span{
    float: right;
  }
  .mask{
    position: absolute;
    z-index: 2;
    height: 100%;
    opacity: 0.5;
    border: 0.5px solid black;
    border-top-right-radius: 5px;
  }
</style>
