<template>
  <div id="submitQuestionnaire">
    <div class="main">
      <div v-if="submitQuestionnaire.length===0">
        <div class="myTitle">
          <p>您还没有填写过调查问卷</p>
        </div>
      </div>
      <div v-else>
        <div class="myTitle">
          <p>这里是我填写过的调查问卷</p>
        </div>
        <div v-for="(item,i) in submitQuestionnaire" :key="i">
          <div class="weui-form-preview">
            <div class="weui-form-preview__hd">
              <div class="weui-form-preview__item">
                <label class="weui-form-preview__label">问卷名</label>
                <em class="weui-form-preview__value">{{item.questionnaire_name}}</em>
              </div>
            </div>
            <div class="weui-form-preview__bd">
              <div class="weui-form-preview__item">
                <label class="weui-form-preview__label">问卷名</label>
                <span class="weui-form-preview__value">{{item.questionnaire_name}}</span>
              </div>
              <div class="weui-form-preview__item">
                <label class="weui-form-preview__label">创建者id</label>
                <span class="weui-form-preview__value">{{item.user_id}}</span>
              </div>
            </div>
            <div class="weui-form-preview__ft">
              <a class="weui-form-preview__btn weui-form-preview__btn_primary">
                <router-link
                  :to="{path:'/mySubmitQuestionnaire',query:{questionnaireID:item.questionnaire_id.toString()}}">
                  <p>查看详细信息</p>
                </router-link>
              </a>
            </div>
          </div>
          <br>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'SubmitQuestionnaire',
  data () {
    return {
      submitQuestionnaire: []
    }
  },
  computed: {
    ...mapGetters([
      'getUserID'
    ])
  },
  created () {
    const temp = this
    this.axios.post('/query_submit_questionnaire', this.getUserID).then((response) => {
      temp.submitQuestionnaire = JSON.parse(response.data.submitQuestionnaire)
      temp.submitQuestionnaire.forEach(function (value, index) {
        temp.submitQuestionnaire[index] = value.fields
      })
    }).catch(function (error) {
      console.log(error)
    })
  }
}
</script>

