<template>
  <div id="editMyPassword">
    <div id="warn" class="weui-toptips weui-toptips_warn">{{warnMessage}}</div>
    <div class="weui-form">
      <div class="weui-form__text-area">
        <h2 class="weui-form__title">修改密码</h2>
        <div class="weui-form__desc">在此处修改您的个人密码</div>
      </div>
      <div class="weui-form__control-area">
        <div class="weui-cells__group weui-cells__group_form">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">原密码</label>
              </div>
              <div class="weui-cell__hd">
                <input type="password" class="weui-input" v-model="nowPassword">
              </div>
              <div class="weui-cell__hd">
                <i id="successNowPassword" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
            <div class="weui-cell weui-cell_active">
              <div class="weui-cell__hd">
                <label class="weui-label">新密码</label>
              </div>
              <div class="weui-cell__hd">
                <input type="password" class="weui-input" v-model="newPassword">
              </div>
              <div class="weui-cell__hd">
                <i id="successNewPassword" class="weui-icon-success weui-icon_msg" style="display: none"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <a class="weui-btn_cell weui-btn_cell-default">
        <p @click="changeMyPassword">修改密码</p>
      </a>
    </div>
    <div class="weui-cells">
      <router-link to="/homePage">
        <a class="weui-btn_cell weui-btn_cell-default">
          <p>返回</p>
        </a>
      </router-link>
    </div>
  </div>
</template>

<script>
import encryption from '../tools/encryption'

export default {
  name: 'EditPassword',
  data () {
    return {
      nowPassword: '',
      newPassword: '',
      warnMessage: ''
    }
  },
  methods: {
    changeMyPassword () {
      if (this.nowPassword === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写原密码',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.newPassword === '') {
        this.$weui.dialog({
          title: '注意',
          content: '请填写新密码',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else if (this.warnMessage) {
        this.$weui.dialog({
          title: '注意',
          content: '请填写正确的用户信息',
          buttons: [{
            label: '确定',
            type: 'primary'
          }]
        })
      } else {
        const params = {
          nowPassword: encryption.encryption(this.nowPassword),
          newPassword: encryption.encryption(this.newPassword),
          userID: this.$store.state.userID
        }
        this.axios.post('/change_user_password', params).then((response) => {
          if (response.data.result === 'wrongPassword') {
            this.$weui.dialog({
              title: '注意',
              content: '原密码输入错误',
              buttons: [{
                label: '确定',
                type: 'primary'
              }]
            })
          } else if (response.data.result === 'success') {
            this.$weui.dialog({
              title: '注意',
              content: '您的密码已经修改成功',
              buttons: [{
                label: '确定',
                type: 'primary',
                onClick: function () {
                  location.reload()
                }
              }]
            })
          } else {
            console.log(response)
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    }
  },
  watch: {
    nowPassword: function () {
      if (!this.nowPassword.match(/^[0-9]{2,20}$/)) {
        this.warnMessage = '请输入正确的密码'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successNowPassword').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successNowPassword').setAttribute('style', 'display:block')
      }
    },
    newPassword: function () {
      if (!this.newPassword.match(/^[0-9]{2,20}$/)) {
        this.warnMessage = '请输入正确的密码'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successNewPassword').setAttribute('style', 'display:none')
      } else if (this.newPassword === this.nowPassword) {
        this.warnMessage = '新密码与旧密码不能一样'
        document.getElementById('warn').setAttribute('style', 'display: block;opacity: 1')
        document.getElementById('successNewPassword').setAttribute('style', 'display:none')
      } else {
        this.warnMessage = ''
        document.getElementById('warn').setAttribute('style', 'display: none;opacity: 0')
        document.getElementById('successNewPassword').setAttribute('style', 'display:block')
      }
    }
  }
}
</script>

<style scoped>
  i{
    width: 25px;
    height: 25px;
  }
</style>
